.cardListComponent {
  width: 30%; 
  max-width: 325px; 
  height: 450px; 
  border: 2px solid grey;
  border-radius: 3px; 
}

.cardListHeader {
  height: 40px; 
  background-color: #4CAF50;
}

.cardListBody {
    display: -ms-flexbox;
    display: flex;                 /* Children use Flexbox */ 
    /* border-top: 2px;  */
    -ms-flex-direction: column-reverse;
        flex-direction: column-reverse; 
    height: 400px; 
    margin: auto;  
    /* border-top: 2px solid grey;  */
}

.cardList {
  width: 100%; 
  height: 90%; 
  padding: 10px; 
  overflow-y: scroll;
}

/** Hide the vertical scroll bar **/ 
.cardList::-webkit-scrollbar {
  display: none;
 }
 
  
.my-button-row {
  width: 100%; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row; 
}
  
.card-button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #4CAF50;
  width: 100%;  
  margin: 10px;
  /* float: right;  */
  /* center button    */
  
  border-radius: 0px;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  -o-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
  
.happy-button {
  margin: 0px;
  margin-top: 10px; 
  margin-right: 10px;  
  margin-left: 10px;  
  border-radius: 2px; 
}

.sad-button {
  background: blueviolet;
  margin: 0px;
  margin-top: 10px; 
  margin-right: 10px;  
  margin-left: 10px;  
  border-radius: 2px; 
}


/******* CARD STUFF ***********/ 

.Happycard {
  width: 100%;                   /* Set width of cards */ 
  border: 1px solid #4CAF50;   /* Set up Border */ 
  border-radius: 2px;            /* Slightly curve edges */ 
  overflow: hidden;              /* Fixes the corners */
  margin-top: 10px;   
  display: -ms-flexbox;   
  display: flex;                 /* Children use Flexbox */ 
  -ms-flex-direction: column; 
      flex-direction: column;        /* Rotate Axis */ 
}
  
.Sadcard {
  width: 100%;                      /* Set width of cards */ 
  border: 1px solid blueviolet;   /* Set up Border */ 
  border-radius: 2px;               /* Slightly curve edges */ 
  overflow: hidden;                 /* Fixes the corners */
  margin-top: 10px;   
  display: -ms-flexbox;   
  display: flex;                    /* Children use Flexbox */ 
  -ms-flex-direction: column; 
      flex-direction: column;           /* Rotate Axis */ 
}  
  
.Happycard-header {
  color: #FFFFFF; 
  text-align: center; 
  font-size: 12px; 
  font-weight: 600; 
  border-bottom: 1px solid #4CAF50; 
  background-color: #4CAF50; 
  padding: 5px 10px; 
}
  
.Sadcard-header {
  color: #FFFFFF; 
  text-align: center; 
  font-size: 12px; 
  font-weight: 600; 
  border-bottom: 1px solid #8a2be2; 
  background-color: #8a2be288; 
  padding: 5px 10px; 
}
  
  
.Happycard-main {
  display: -ms-flexbox;
  display: flex;              /* Children use Flexbox */ 
  -ms-flex-direction: row; 
      flex-direction: row;        /* Rotate Axis to Vertical */ 
  -ms-flex-pack: center; 
      justify-content: center;    /* Group Children in Center */ 
  -ms-flex-align: center; 
      align-items: center;        /* Group Children in Center (+axis) */ 
  padding: 15px 0;            /* Add padding to the top/bottom */ 
}
  
.Sadcard-main {
  display: -ms-flexbox;
  display: flex;              /* Children use Flexbox */ 
  -ms-flex-direction: row; 
      flex-direction: row;        /* Rotate Axis to Vertical */ 
  -ms-flex-pack: center; 
      justify-content: center;    /* Group Children in Center */ 
  -ms-flex-align: center; 
      align-items: center;        /* Group Children in Center (+axis) */ 
  padding: 15px 0;            /* Add padding to the top/bottom */ 
}
  
.Happymain-description {
  color: #4CAF50; 
  font-size: 12px; 
  text-align: center 
}
  
.Sadmain-description {
  color: #4CAF50; 
  font-size: 12px; 
  text-align: center 
}
  
.Sad {
  color: blueviolet; 
}
  
@media screen and (max-width: 600px){
  .cardListComponent {
    -ms-flex-direction: column;
        flex-direction: column; 
    width: 90%; 
  }
  .cardList{
    width: 100%; 
    -ms-flex-order: 2; 
        order: 2; 
  }
  .my-button-row{
    -ms-flex-order: 1;
        order: 1; 
    width: 100%; 
  }
}


